$fz_base: 16;
$fz: $fz_base;
// ##########
//    
// ##########
$container-width: 1300; //    
$container-padding: 10; //      
$grid-container-width: 100%; //   
$grid-cols: 12; //    
$grid-gap: 30; //   
$grid-gap-vertical: 30; //     
$breakpoints: (
  'xl': 1920px,
  'lg': 1680px,
  'md': 1024px,
  'sm': 768px,
  'xs': 480px,
);

// ##########
// 
// ##########
$color-basic: black; //    (  body)
$color-placeholder: #777; //    placeholder
$color-input: black; //   input, select  textarea
$color-main: #2252d8; //   
$color-border: #ccc; //    , 

// ##########
// Transitions
// ##########
$ease-main: 0.2s ease;

// ##########
// 
// ##########
$font-main: "Gilroy", sans-serif; //   
$font-second: "Arial", sans-serif; //  


